.bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    background: linear-gradient(
      135deg,
      #f9f9f9 48%,
      #ebebeb 48%,
      #ebebeb 78%,
      #f9f9f9 78%
    );
  }
  
  .container {
    /* display: flex; */
    align-items: flex-end;
    margin: 120px auto 226px;
    justify-content: space-around
  }

  .box {
    box-shadow: -4px 10px 35px -1px rgba(0, 0, 0, 0.75);
    min-height: 20rem;
    margin: 30px;
    padding: 20px;
    border-radius: 5px;
  }

  .box2 {
      display: flex;
      flex-direction: column;
  }
  
  .texts {
    display: flex;
    flex: 1 1;
    flex-direction: column;
    justify-content: center;
    margin-right: 100px;
  }
  
  .heading {
    margin: 15px 0;
    font-weight: 500;
    line-height: 1.39;
  }
  
  .description {
    margin: 15px 0px 35px;
    font-size: 15px;
    line-height: 1.61;
    color: #878787;
    text-align: left;
    letter-spacing: normal;
  }
  
  .figure {
    flex: 1 1;
    padding: 0px;
    text-align: center;
  }
  
  .figure img {
    width: 100%;
    max-width: 418px;
  }

  .btn_center{
    display: flex;
    justify-content: center;
}
  
  @media (max-width: 840px) {
    .container {
      flex-direction: column-reverse;
      margin: 60px auto 226px;
    }
  
    .text {
      margin-right: 0;
    }
  
    .figure {
      margin-bottom: 40px;
    }
  }
  